@import './styles/Variables.scss';
 /*----------------------App-----------------------*/
.App {

  
  &--Light-Mode {

    & h1 {
      color: $Theme-ColorBlue;
    }

    & h3,p,li {
      color: rgb(68, 68, 68);
    }

    & .Mask {
      background: linear-gradient(45deg, $Theme-ColorGreen--LM, $Theme-ColorBlue--LM);
    }

  }

}
 /*----------------------All pages-----------------------*/
.DarkMode-btn {
    display: flex;
    position: absolute;
    right: 4%;
    top: 4%;
    cursor: pointer;
    z-index: 10;
    border: none;
    font-size: 1em;
    width: 1em;
    height: 1em;
    background: transparent;
    transition: all .2s ease-in-out;
}

.DarkMode-btn:hover, .DarkMode-btn:focus {
   transform: scale(1.4);
}

.DarkMode-btn:focus {
  outline: none;
  transform: scale(1.4);
}

.Mask {
   position: absolute;
   top: 0;
   left: 0;
   opacity: 0;
   width: 100%;
   min-height: 100%;
   background: linear-gradient(45deg, $Theme-ColorBlue, $Theme-ColorGreen);
   mask-image: url(./svg/Background-Lines.svg);
   mask-repeat: no-repeat;
   mask-position: center;
   mask-size: cover;
   z-index: -1;
}

.Fade {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 90px;
  height: 12%;
  background: linear-gradient(rgba(13, 22, 38, 0) 0%, rgba(13, 22, 38, 1) 50%);

  &--Light-Mode {
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    min-height: 90px;
    height: 12%;
    background: linear-gradient(rgba(247, 255, 246, 0) 0%, rgba(247, 255, 246, 1) 50%);
  }
}
  /*----------------------Icons-----------------------*/
.icon {
  font-size: 1em;
  margin: 0 .5em 0 0;

  @media (min-height: 860px) {
    font-size: 1.5em;
  }

  &-gsap {
    display: inline-block;
    height: 1em;
    width: 1em;
  }

  &-contact {
    font-size: 1em;
  }
}

