@import './Variables.scss';
.About {
    width: 100%;
    min-height: 100vh;

    &-Container {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 100px 10% 15% 10%;

        &-Header {
            font-size: 2rem;
            color: #29D9B9;
            margin-bottom: 25px;
        }
    
        &-Text {
            background-color: rgba(196, 196, 196, 0.05);
            border-radius: 1rem;
            padding: 1rem;
            margin-bottom: 25px;
            & span {
                display: inline-block;
                margin-bottom: 1rem;
            }
        }

    }
}